$("._sum-input").change(function () {
    let val = $(this).val()
    if(val < 150){
        $(this).parents(".fg").addClass("_error")
    } else{
        $(this).parents(".fg").removeClass("_error")
    }
})

$(".sum-field input").change(function () {
    setSum()
})

setSum()

function setSum() {
    let sum = +$(".sum-field input:checked").siblings("label").find("span").html()
    $("._sum").html(sum)
}
